.extractionList {
    z-index: 2;
    width: 100%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;

    background: linear-gradient(115deg, #624e69 0%, #7f608a 40%, #624e69 80%);
    
    color: #ece5d9;

    padding: 8px 0;
    box-sizing: border-box;

    overflow: hidden;

    .__content {
        width: 100%;
        height: 100%;
        padding: 8px;
        padding-top: calc(var(--header_height) + var(--inset_top));
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        align-content: flex-start;
        overflow-y: auto;
    }
}