.aboutSector {
    width: 100%;
    padding: 8px 10px;
    box-sizing: border-box;

    .__container {

        // &.__one {
        //     padding-bottom: 6px;
        //     border-bottom: 1px solid #5a5a5a;
        // }

        // &.__two {
        //     padding-top: 6px;
        //     padding-bottom: 6px;
        //     margin-bottom: 6px;
        //     border-bottom: 1px solid #5a5a5a;
        // }

        .__item {

            border-bottom: 1px solid #5a5a5a;

            &:last-child {
                border-bottom: 0px;
            }

            width: 100%;
            overflow: hidden;
            display: flex;
            align-items: center;

            padding: 10px 0;

            color: white;
            line-height: normal;
            font-size: 13px;


            .__icon {
                width: 16px;
            }

            .__name {
                padding-left: 10px;

            }

            .__text {
                flex: 1;
                padding-left: 6px;
                white-space: nowrap;
            }

        }

    }
}

